import { FEATURE_REQUEST } from "./api";
import HttpClient from "./http-client";
export const IS = localStorage.getItem('token');


export const featureRequest = (request) => {
       return new HttpClient().post(`${FEATURE_REQUEST}`, {
              'name': request.name,
              'email': request.email,
              'description': request.description,
              'file':request.file
       })
}
