import React, { Component } from 'react';
import $ from 'jquery';
import { Uppy } from "@uppy/core";
import '@uppy/core/dist/style.min.css';
import '@uppy/dashboard/dist/style.min.css';
import '@uppy/drag-drop/dist/style.min.css';
import '@uppy/status-bar/dist/style.min.css';
import '@uppy/webcam/dist/style.min.css';
import Transloadit from "@uppy/transloadit";
import GoogleDrive from '@uppy/google-drive';
import Url from '@uppy/url';
import { DashboardModal } from '@uppy/react';
import { Image, Button } from 'react-bootstrap';
import { TEMP_URL, UPPY_BUCKET_URL, UPPY_KEY, UPPY_ROBOT, UPPY_BUCKET, UPPY_REGION, UPPY_AWS_KEY, UPPY_AWS_SECRET_KEY, UPPY_COMPANION_URL } from '../../../../../actions/constants';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';


export default class AttachMedia extends Component {
    constructor(props) {
        super(props)
        this.uppy = new Uppy({ debug: true, autoProceed: false, restrictions: { maxFileSize: 50000000, maxNumberOfFiles: 1, minNumberOfFiles: 1, allowedFileTypes: this.props.allowFiles } })
            .use(Transloadit, {
                target: '#a',
                params: {
                    auth: {
                        key: UPPY_KEY
                    },
                    steps: {
                        store: {
                            robot: UPPY_ROBOT,
                            bucket_region: UPPY_REGION,
                            bucket: UPPY_BUCKET,
                            key: UPPY_AWS_KEY,
                            secret: UPPY_AWS_SECRET_KEY,
                            path:this.props.isLocation ? 'request-feature/${file.id}.${file.ext}' : 'tmp/${file.id}.${file.ext}'
                        }
                    }
                },
                waitForEncoding: true
            })
            .use(GoogleDrive, { target: Uppy.Dashboard, companionUrl: UPPY_COMPANION_URL })
            .use(Url, { target: Uppy.Dashboard, companionUrl: UPPY_COMPANION_URL })
    }
    state = {
        backgroundModal: false,
        ext:'',
        fileName:''
    }
    componentDidMount() {
        var self = this;
        this.uppy.on('transloadit:result', (stepName, result) => {
            this.setState({
                ext:result.ext,
                fileName:result.basename
            })
            self.props.onClickToupload(this.props.isLocation ? `${UPPY_BUCKET_URL}request-feature/${result.id}.${result.ext}` : `${TEMP_URL}${result.id}.${result.ext}`, () => {
                setTimeout(() => {
                    this.uppy.cancelAll()
                    this.setState({ backgroundModal: false })
                }, 300)
            })
        })
    }
    onBackgroundClose = event => {
        this.uppy.cancelAll()
        this.setState({ backgroundModal: false })
    }
    onBackgroundOpen = event => {
        this.setState({ backgroundModal: true }, () => {
            $(".uppy-Dashboard-close").click(() => {
                this.uppy.cancelAll()
                this.setState({ backgroundModal: false })
            })
        })
    }
    render() {
        const { imageStyle, uploadedMedia, id, text } = this.props
        const { backgroundModal, ext, fileName } = this.state

        const fileIcon = (ext) => {
            switch(ext) {
                case 'docx':
                    return 'file-doc'
                    break;
                case 'pdf':
                    return 'file-pdf'
                    break;
                case 'mp4':
                    case 'mov':
                    return 'file-video'
                    break;
                default:
                    return false
            }
        }
        return <>
            <div className="d-flex align-items-start justify-content-between flex-column py-1">
                {uploadedMedia ?
                    <div className='f-between w-100 bg-gray-100 rounded-2 p-4 align-items-center'>
                        <div className='d-flex flex-grow-1 align-items-center' style={{width:'calc(100% - 30px)'}}>
                            {fileIcon(ext)?
                                <div className='f-center flex-shrink-0 me-3' style={{fontSize:'3rem'}}>
                                    <FontAwesomeIcon type={'solid'} icon={fileIcon(ext)} iconClass="text-gray-700"/>
                                </div>
                            :
                                <Image id="bg_image" className="object-fit-cover rounded-2 me-3" src={uploadedMedia} height={60} width={60} alt="" style={{ maxHeight: 60 }} />
                            }
                            {ext ? <p className='text-ellipsis fs-8 mb-0'>{fileName}.{ext}</p> : null}
                        </div>
                        <Button variant="icon" size="sm" className="rounded-circle bg-danger border-danger text-white h-20px w-20px" onClick={() => this.props.onClickToupload('', () => { })}>
                                <FontAwesomeIcon icon={'xmark'}/>
                        </Button>
                    </div> 
                    :
                    <>
                    <Button id={id} variant='light' className="w-100" onClick={() => this.setState({ backgroundModal: true })}><FontAwesomeIcon icon={'arrow-up-from-bracket'} />{text ? text : 'Upload'}</Button>
                    <p className='text-gray-500 mt-1 fw-medium fs-9'><FontAwesomeIcon type={'solid'} icon={'info-circle'} iconClass={'me-1'} />Accepts {this.props.allowFiles.join(", ")}</p>
                    </>
                }
            </div>
            {
                backgroundModal ?
                    <DashboardModal
                        animateOpenClose={true}
                        open={backgroundModal}
                        proudlyDisplayPoweredByUppy={false}
                        inline={false}
                        showProgressDetails={true}
                        height={750}
                        browserBackButtonClose={true}
                        showRemoveButtonAfterComplete={false}
                        uppy={this.uppy}
                        onRequestClose={this.onBackgroundClose}
                        closeAfterFinish={true}
                        plugins={['Url']}
                    />
                    : null
            }
        </>
    }

}
