import React, { Component } from 'react'
import { Modal, Button, Col, Row, InputGroup, Form } from 'react-bootstrap'
import { connect } from "react-redux";
import AttachMedia from './Uploads/AttachMedia'
import { containsSpecialChars, TOAST_ERROR, TOAST_SUCCESS, validateEmail } from '../../../../utils';
import { featureRequest } from '../../../../actions/appActions';

class ContactModal extends Component {
  constructor(props) {
      super(props);
      this.state = {
        name:this.props.authorized?.userData?.user_data?.user.name,
        email:this.props.authorized?.userData?.user_data?.user.emailId,
        description:'',
        uploadedMedia:'',
        uploadedUrl:'',
        nameErr:false,
        emailErr:false,
        descriptionErr:false,
        isLoading:false
      };
  }

  
  submitForm = () => {
      const {name, email, description, uploadedMedia} = this.state

      let nameValidation = name && !containsSpecialChars(name) && String(name).trim() != 0 ? true : false
      let emailValidation = email && validateEmail(email) ? true : false
      let descriptionValidation = description && description.length <= 500 ? true : false

      if (nameValidation && emailValidation && descriptionValidation) {
        this.setState({
          isLoading:true
        })
        const request = { 'name': name, 'email': email, 'description':`<b>${this.props.title}</b>-${description}`, 'file': uploadedMedia}
        featureRequest(request).then((res) => {
          if (res.data.responseCode == 200) {
            setTimeout(() => {
              TOAST_SUCCESS('Request shared successfully')
              this.setState({
                isLoading:false,
                name:this.props.authorized?.userData?.user_data?.user.name,
                email:this.props.authorized?.userData?.user_data?.user.emailId,
                description:'',
                uploadedMedia:'',
                uploadedUrl:'',
                nameErr:false,
                emailErr:false,
                descriptionErr:false
              })
              this.props.onCloseModel(false)
            }, 1000);
          } else {
            this.setState({
                isLoading:false
            })
            TOAST_ERROR(res.data.message);
          }
        }).catch((error) => {
          this.setState({
              isLoading:false
          });
          TOAST_ERROR('Something went wrong, please try again later.');
        })
      }else{
        this.setState({
            isLoading:false,
            nameErr: !nameValidation,
            emailErr: !emailValidation,
            descriptionErr: !descriptionValidation
        });
      }
    }
  
  onClickToupload = (value, callBack) => this.setState({ uploadedMedia: value }, () => callBack())

  onCloseModelFun = () => {
      this.setState({
        name:this.props.authorized?.userData?.user_data?.user.name,
        email:this.props.authorized?.userData?.user_data?.user.emailId,
        description:'',
        uploadedMedia:'',
        uploadedUrl:'',
        nameErr:false,
        emailErr:false,
        descriptionErr:false
      })
      this.props.onCloseModel(false)
  }
  render() {
    const { show, onCloseModel, authorized, title } = this.props
    const user = authorized?.userData?.user_data?.user
    const {name, email, description, uploadedUrl, nameErr, emailErr, descriptionErr, isLoading, uploadedMedia} = this.state
    return (<Modal id="reqFeature" size="lg" show={show} aria-labelledby="reqFeature" tabIndex="0" centered backdrop={'static'} onHide={this.onCloseModelFun}>
        <Modal.Header closeButton>
          <Modal.Title>{title}</Modal.Title>
        </Modal.Header>
        <Modal.Body className="p-lg-7 p-3 demo_schedule position-relative">
          <Row className="mb-3">
            <Form.Group as={Col} md="6">
              <Form.Label>Full Name <strong className="text-danger">*</strong></Form.Label>
              <Form.Control
                id="r_f_name"
                required
                type="text"
                placeholder="Full name"
                className={nameErr ? 'is-invalid' : ''}
                value={name} onChange={(e) => this.setState({name:e.target.value, nameErr:false})}
              />
              {nameErr && <div className="invalid-feedback">Name is required.</div>}
            </Form.Group>
            <Form.Group as={Col} md="6">
              <Form.Label>Email <strong className="text-danger">*</strong></Form.Label>
              <Form.Control
                id="r_f_email"
                required
                type="email"
                placeholder="example@example.com"
                className={emailErr ? 'is-invalid' : ''}
                value={email} onChange={(e) => this.setState({email:e.target.value, emailErr:false})}
              />
              {emailErr && <div className="invalid-feedback">{email != '' ? 'Enter valid email.' : 'Email ID is required.'}</div>}
            </Form.Group>
          </Row>
            <Form.Group as={Col} className="mb-3">
              <Form.Label>Description <strong className="text-danger">*</strong></Form.Label>
              <Form.Control as={'textarea'} id="r_f_desc"
                placeholder="Enter Description"
                className={`${descriptionErr ? 'is-invalid' : ''} textarea-min-row-6`} maxLength={500} onChange={(e) => this.setState({description:e.target.value, descriptionErr:false})} value={description}
              />
              <div className="f-between">
                <div className="invalid-feedback d-block">{descriptionErr ? 'This field is required' : ''}</div>
                <span className="fs-10 text-muted pt-1">{description?.length}/500</span>
              </div>
            </Form.Group>
            <Form.Group as={Col} className="mb-3">
              <AttachMedia id="sugg_doc" imageStyle={{ paddingBottom: '66%' }} uploadedMedia={uploadedMedia} onClickToupload={this.onClickToupload} text={'Upload File'} allowFiles={['image/*', '.docx', '.pdf', '.mp4', '.mov']} isLocation={true} />
            </Form.Group>
        </Modal.Body>
        <Modal.Footer>
          <Button id="r_f_cancel" variant='secondary' className='ms-3 btn-min-w' onClick={this.onCloseModelFun}>Cancel</Button>
          <Button id="r_f_submit" className={`ms-3 btn-min-w ${isLoading? 'spinner' :''}`} onClick={this.submitForm}>Submit</Button>
        </Modal.Footer>
      </Modal>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    authorized: state.authorized,
  }
}
const mapDispatchToProps = dispatch => {
  return {

  }
}
export default connect(mapStateToProps, mapDispatchToProps)(ContactModal);