import React, { Component } from 'react';
import { Button, Tooltip, OverlayTrigger } from 'react-bootstrap'
import { FontAwesomeIcon } from '../../../../Assets/AssetsHelper'
import ContactModal from './ContactModal'

class RequestButtonHOC extends Component {
       constructor(props) {
              super(props);
              this.state = {
                     show: false
              };
       }
       render() {
              const {title} = this.props
              const { show } = this.state
              return <>
                     <span onClick={() => this.setState({ show: !show })}>
                            {this.props.children}
                     </span>
                     <ContactModal show={show} onCloseModel={() => this.setState({ show: false })} title={title ? title : 'Request New Feature'}/>
                     </>
       }
}

export default RequestButtonHOC;