import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { Link, useNavigate, useParams } from 'react-router-dom';
import { GET_ADD_FEED } from '../../../../../actions/moderationActions';
import { SNAPUP_DETAILS_ACTION } from '../../../../../actions/addFeedActions';
import { GET_LOCAL, GET_NETWORK_ICON, GET_NETWORK_NAME, SET_LOCAL, generateRandomArray } from '../../../../../utils';
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { Image, Badge, Container, Card, Button } from 'react-bootstrap';
import RequestButtonHOC from '../../CommonComponents/RequestButtonHOC';
import ContactModal from '../../PricePlan/Elements/ContactModal';
import { checkPermission } from '../../../../../actions/collaboratorActions';
import SubHeaderCrumbs from '../../CommonComponents/SubHeader';
import UpgradePlan from '../UpgradePlan';
import PaymentModal from '../../PricePlan/Elements/PaymentModal';
import { getPlan } from '../../../../../actions/planDetail';
import { HUBSPOT_URL, PRODUCT_PLANS } from '../../../../../actions/constants';
import { updateSalesModal } from '../../../../../actions/loginActions';

const AddFeed = () => {
    const { wallId } = useParams();
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const addFeedData = useSelector(state => state.addFeedReducer)
    const allWalls = useSelector(state => state?.dashboardData?.dashboarddata?.allWalls)
    const [showContactUs, setShowContactUs] = useState(false)
    const [type, setType] = useState("")
    const [payment, setPayment] = useState(false)
    const [filterNetwork, setFilterNetwork] = useState([])
    const [network, setNetwork] = useState(null);

    useEffect(() => {
        dispatch(GET_ADD_FEED())
    }, [])

    const getLinkProps = (networkId, access) => {
        if (access == 0) return {}
        if (networkId == 45 || networkId == 46) return {};
        if (networkId === 33) return { to: "/creators" };
        if (networkId === 0) return { to: "/manual-upload" };
        return { to: `/content/addfeed/${wallId}/${networkId}` };
    };

    const clickToshowContact = (status, key) => {
        setShowContactUs(status)
        setType(key)
    }

    const checkTour = async (sel) => {
        const tourStep = GET_LOCAL('tourStep');
        const isTourComplete = GET_LOCAL('isTour') ?? true;
        const { default: appTour } = await import('../../../../../actions/appTour')
        const { tour } = new appTour(navigate, false, dispatch);
        if (!isTourComplete && tourStep?.id == '#tour_net_18' && addFeedData?.addFeedNetworks?.networks?.length) {
            SET_LOCAL('tourStep', { id: "#create_feed", step: 2 });
            if (sel) tour.destroy();
            else setTimeout(() => tour.highlight({ element: '#tour_net_18' }), 400);
        }
    }

    const getPaidPlan = (type, loadingCB) => {
        const postBody = {
            billingcycle: "0",
            isAddOn: "0",
            couponCode: "",
            widget_addon_qty: "0",
            feeds_addon_qty: "0",
            twitter_addon_qty: "0",
            isCreatenewSubscription: false,
            planId: PRODUCT_PLANS.firstPaid
        }
        getPlan(postBody, type, loadingCB);
    }

    useEffect(() => {
        checkTour();
        setFilterNetwork(addFeedData?.addFeedNetworks?.networks);
    }, [addFeedData?.addFeedNetworks?.networks])

    const renderNetworkBlocks = (network) => {
        return <div key={network.id} className="px-2 mb-4">
            <Link id={`tour_net_${network.id}`} className='btn btn-secondary position-relative' {...getLinkProps(network.id, network.access)} onClick={() => network.access == 0 ? setNetwork(network) : [34, 35].includes(network.id) ? clickToshowContact(true, network.name) : [45, 46].includes(network.id) ? dispatch(updateSalesModal({ url: `${HUBSPOT_URL}`, embedCode: null })) : localStorage.getItem('tourStep') ? checkTour(true) : {}} title={GET_NETWORK_NAME(network.id)}>
                {network.id == 29 ?
                    <FontAwesomeIcon type={'solid'} icon={'star'} iconClass={'text-warning fs-6'} />
                    :
                    <>
                        <Image height={20} width={20} className='me-2' src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(network.id)}.svg`)} alt={GET_NETWORK_NAME(network.id)} title={GET_NETWORK_NAME(network.id)} fluid />
                        {network.access == 0 ? <Image style={{ position: "absolute", right: '5px', top: '-1px', maxHeight: '14px' }} src={baseUrl(`media/icons/social-icons-square/premium.svg`)} /> : null}
                    </>
                }
                {network.name}
                {network.id === 33 && <Badge bg="" className='position-absolute badge-primary py-1' style={{ top: -16, right: 10 }}>New</Badge>}
            </Link>
        </div >
    }

    const getWallData = allWalls?.find((e) => e.id == localStorage.getItem('wallId')) ?? {};

    const filterAction = (status) => {
        let networkList = addFeedData?.addFeedNetworks?.networks;

        if (networkList && status) {
            let isReview = true;
            if (status == "social") isReview = false;
            networkList = networkList.filter(item => {
                if (item.id == 3 || item.isReview == isReview) return item;
            });
        }
        setNetwork(status);
        setFilterNetwork(networkList);
    }

    return (<>
        {allWalls?.length ? <SubHeaderCrumbs key={getWallData?.id} crumbs={[{ name: getWallData?.name ?? "", icon: "grid-2-plus", imgUrl: getWallData?.thumbnail ?? "" }, { name: "Home", link: "/home" }, { name: "Content", link: "/content" }, { name: getWallData?.name, link: `/content/moderation/${getWallData?.id}` }, { name: "Add feed", link: `/content/addfeed/${getWallData?.id || localStorage.getItem('wallId') || ''}` }]} RightComponent={<div>
            {
                <button onClick={() => checkPermission('networkManager', 'manageFeed') ? navigate(`/content/managefeed/${localStorage.getItem('wallId')}`) : {}} className="btn btn-primary btn-sm text-nowrap btn-min-w">
                    Manage feed
                </button>
            }
        </div>} showGallerySwitcher={false} /> : null}

        <div className='d-flex flex-column flex-column-fluid full-content'>
            <Container>
                <Card>
                    {/* <Card.Header className='f-between d-flex'>
                        <h2 className="me-2 h4 my-3">Add feeds</h2>
                        <div className="f-center">
                            {manageFeed.feeds?.length > 0 ? <Link to={`/content/managefeed/${localStorage.getItem('wallId')}`} className="btn btn-primary btn-sm text-nowrap btn-min-w" title='feed'>
                                Manage feeds
                            </Link> : null}
                        </div>
                    </Card.Header> */}
                    <Card.Body>
                        <div className='f-center add_feed_ m-auto'>
                            <div className='f-center flex-column mb-10'>
                                <div className="text-center mb-5">
                                    <h3>Choose source network</h3>
                                    <p>Select the social platform from which you want to add the feeds</p>
                                </div>

                                {/* Filter networks start */}
                                <div className="network-filter tb_filter_round_edged_wrapper">
                                    <div className="row">
                                        <div className="col-12 px-0 d-flex flex-row align-items-center justify-content-between mb-5 pb-3">
                                            <div className="filer-custom-desgin d-flex">
                                                <div className={`d-flex align-items-center jsutify-content-center mx-1`} >
                                                    <div className={`rounded-0 border-0 d-flex align-items-center`}> <Button variant='outline-dark' className={network === null ? 'active btn-dark' : ''} onClick={() => filterAction(null)}>All Networks</Button></div>
                                                </div>
                                                <div className={`d-flex align-items-center jsutify-content-center mx-1`} >
                                                    <div className={`rounded-0 border-0 d-flex align-items-center`}>
                                                        <Button variant='outline-dark' className={network === 'review' ? 'active btn-dark' : ''} onClick={() => filterAction('review')}>Review</Button>
                                                    </div>
                                                </div>
                                                <div className={`d-flex align-items-center jsutify-content-center mx-1`} >
                                                    <div className={`rounded-0 border-0 d-flex align-items-center`}>
                                                        <Button variant='outline-dark' className={network === 'social' ? 'active btn-dark' : ''} onClick={() => filterAction('social')}>Social</Button>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                {/* Filter networks end */}

                                <div className="f-center flex-wrap mb-10">
                                    {filterNetwork?.length > 0 ?
                                        filterNetwork.map(renderNetworkBlocks)
                                        :
                                        generateRandomArray(18).map((_, index) => <div key={index} className="px-2 mb-4"><div className="sk_rect h-40px me-2 btn-min-w" style={{ width: 120 }}></div></div>)}

                                    {filterNetwork?.length > 0 ? <div className='px-2 mb-4'>
                                        <RequestButtonHOC title={'Request New Network'}>
                                            <Button variant='secondary'><FontAwesomeIcon icon={'plus'} />Request New Network</Button>
                                        </RequestButtonHOC>
                                    </div> : null}
                                </div>
                            </div>
                        </div>
                    </Card.Body>
                </Card>
            </Container>
            <ContactModal show={showContactUs} type={type} onCloseModel={() => clickToshowContact(false, "")} />

            {network?.id ? <UpgradePlan network={network} showPayment={() => setPayment(true)} onHide={() => setNetwork(null)} showPricing={() => navigate(`/price`)} /> : null}
            <PaymentModal show={payment} onHide={() => setPayment(false)} paymentAction={(type, loadingCallBack) => getPaidPlan(type, loadingCallBack)} />
        </div>
    </>)
}

export default AddFeed